<template>
  <div class="app-container">
    <van-form @submit="onSubmit">
      <van-field v-model="ticket.item_name" label="工单名称" readonly />
      <van-field v-model="ticket.name" label="运送路径" readonly />
      <van-field v-model="ticket.source" label="工单来源" readonly />
      <van-field v-model="ticket.state" label="工单状态" readonly>
        <template #input>
          <van-tag type="primary">{{ ticket.state | statusFilter }}</van-tag>
        </template>
      </van-field>
      <van-field v-model="ticket.create_time" label="下单时间" readonly />
      <van-field v-model="ticket.get_time" label="取件时间" readonly />
      <van-field v-model="ticket.complete_time" label="完成时间" readonly />
      <van-cell title="手写签名" value="" />
      <van-uploader v-model="fileList" multiple :max-count="5" :after-read="afterRead" :max-size="1024 * 1024" :deletable="false" :show-upload="false" style="float: left;" @oversize="onOversize" />
    </van-form>
  </div>
</template>

<script>
export default {
  name: 'TransportTicketInfo',
  filters: {
    statusFilter(val) {
      const map = {
        0: '未开始',
        1: '已派工',
        2: '运送中',
        10: '已完成',
        20: '已过期'
      }
      return map[val]
    }
  },
  data() {
    return {
      ticket_id: '',
      operate: '', // 操作类型：view查看
      ticket: {},
      fileList: []
    }
  },
  async mounted() {
    this.ticket_id = this.$route.query.ticket_id
    this.ticket = await this.$http.post('/transport-ticket/get', { ticket_id: this.ticket_id })
    if (this.ticket.imagelist && this.ticket.imagelist.length > 0) {
      const imageArr = []
      this.ticket.imagelist.forEach(item => {
        imageArr.push({ url: item, isImage: true })
      })
      this.fileList = imageArr
    }
  },
  methods: {
    sourceFilter(val) {
      const map = {
        '1': '客户发起',
        '2': '系统自动发起',
        '3': '服务点发起'
      }
      return map[val]
    },
    afterRead(file) {
      // 此时可以自行将文件上传至服务器
    },
    onOversize(file) {
      this.$toast('文件大小不能超过 1024kb')
    },
    onSubmit() {

    }
  }
}
</script>

<style lang="less" scoped>
.app-container {
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #fff;
  color: rgb(153, 153, 153);
  p {
    margin: 0 0 5px 0;
  }
  .row{
    display: flex;
    justify-content: space-between;
  }
}
</style>
